import Link from 'next/link';

export default () => (
  <div>
    <div className="primary" style={primaryStyle}>
      <span className="logo">Appinda</span>
      <Link href="/">
        <a>Home</a>
      </Link>
      <Link href="/about">
        <a>About</a>
      </Link>
    </div>
  </div>
);

const linkStyle = {
  marginRight: 15
};

const primaryStyle = {
  height: "100px",
  background: "rgb(255,170,170)",
  background: "linear-gradient(9deg, rgba(255,170,170,1) 0%, rgba(255,0,0,1) 100%)"
}
